(() => {
	let $inputs = $('.form__input');
	let clientId;

	if ($inputs.length) {
		$inputs.each((index, element) => {
			if ($(element).val() !== '') {
				$(element).parents('.form__label').addClass('is-active');
			}
		});

		$inputs.focus((event) => {
			let $element = $(event.currentTarget);

			$element.parents('.form__label').addClass('is-active');
		}).blur(() => {
			let $element = $(event.currentTarget);

			if ($element.val() === '') {
				$element.parents('.form__label').removeClass('is-active');
			}
		});

		ga((tracker) => {
			clientId = tracker.get('clientId');

			$('input[name="clientId"]').val(clientId);
		});
	}
})();
