import vars from './vars';
import 'slick-carousel';

if (vars.$slider.length) {
	vars.$slider.slick({
		dots: true,
		arrows: true,
		infinite: true,
		variableWidth: false,
		slidesToShow: 5,
		slidesToScroll: 1,
		nextArrow: '<span class="slider__controls__next slick-next"><img src="images/next.svg" alt=""></span>',
		prevArrow: '<span class="slider__controls__prev slick-prev"><img src="images/prev.svg" alt=""></span>',
		appendArrows: vars.$sliderControls,
		appendDots: vars.$sliderPaginations,
		speed: 500,
		accessibility: true,
		responsive: [{
			breakpoint: 1024,
			settings: {
				slidesToShow: 3,
				infinite: true,
			},
		}, {
			breakpoint: 768,
			settings: {
				slidesToShow: 1,
				dots: true,
			},
		}],
	});
}
