import Parser from 'ninelines-ua-parser';

const parser = new Parser();
let mobile = false;

if (parser.getDevice().type === 'mobile' || parser.getDevice().type === 'tablet' || innerWidth <= 768) {
	mobile = true;
}

export {mobile};

export default {
	$document: $(document),
	$window: $(window),
	$body: $('body'),

	$videoWrapper: $('.js-video-wrapper'),
	$manualStepNumber: $('.js-manual-step-number'),

	$slider: $('.js-slider'),
	$sliderControls: $('.js-slider-controls'),
	$sliderPaginations: $('.js-slider-paginations'),
};
