import vars from './vars';
const YTPlayer = require('yt-player');

if (vars.$videoWrapper.length) {
	const player = new YTPlayer('#video', {
		related: false,
		width: 508,
		height: 286,
	});
	let videoId = $('#video').data('video-id');
	let isPlayed = false;

	player.load(videoId);
	player.setVolume(0);

	vars.$window.scroll(() => {
		let condition1 = vars.$window.scrollTop() > vars.$videoWrapper.offset().top - innerHeight / 2;
		let condition2 = vars.$window.scrollTop() < vars.$videoWrapper.offset().top + innerHeight / 2;

		if (condition1 && condition2 && !isPlayed) {
			isPlayed = true;
			player.play();
		} else if ((!condition1 || !condition2) && isPlayed) {
			isPlayed = false;
			player.pause();
		}
	});
}
