(($) => {
	let $form = $('.form');
	let message = $('.msg');
	let messageText = $('.msg__content__text');
	let $document = $(document);
	let $currentForm;

	// Success function
	function done(response) {
		let id = $currentForm.find('input[name="form"]').val();

		$currentForm.find('input').val('');
		$currentForm.find('.form__label').removeClass('is-active');

		$document.on('successDone', () => {
			document.location.href = 'success.html';
		});

		$document.trigger('success', [id]);
	}

	// Fail function
	function fail(data) {
		message.fadeIn().addClass('is-active').removeClass('alert-success').addClass('alert-danger');
		messageText.text(data.responseText);
		setTimeout(() => {
			message.fadeOut().removeClass('is-active');
		}, 2000);
	}

	$form.submit((e) => {
		e.preventDefault();
		$currentForm = $(e.currentTarget);
		let formData = $currentForm.serialize();

		$form = $currentForm;

		$.ajax({
			type: 'POST',
			url: $form.attr('action'),
			data: formData,
		})
			.done(done)
			.fail(fail);
	});
})(jQuery);
