import vars from './vars';

if (vars.$manualStepNumber.length) {
	vars.$manualStepNumber.each((index, element) => {
		vars.$window.scroll(() => {
			let condition1 = vars.$window.scrollTop() > $(element).offset().top - innerHeight / 1.5;
			let condition2 = vars.$window.scrollTop() < $(element).offset().top + innerHeight / 1.5;

			if (condition1 && condition2) {
				$(element).addClass('is-active');
			} else {
				$(element).removeClass('is-active');
			}
		});
	});
}
