import vars from './vars';

function clearText(text) {
	return text.toString().trim().replace(/\s+/g, ' ');
}

vars.$document.on('success', (event, id) => {
	ga('wc.send', 'event', 'page', 'success', `form - ${id}`);
	ga('send', 'event', 'landing', 'success', `form - ${id}`);

	vars.$document.trigger('successDone');
});
